<template>
  <div id="app" @mousemove="toggleFullScreen($event)">
    <div class="full-screen-section" ref="fullScreenEle">
      <button class="btn left" @click="fullScreen" v-show="!isFullScreen">全屏</button>
      <button class="btn left" @click="exitFullScreen" v-show="isFullScreen">退出全屏</button>
      <button class="btn right" @click="setDraw">设置</button>
    </div>
    <header class="header" ref="header">
    </header>
    <main id="main" class="main">
      <section class="left" v-show="!isInitData">
        <draw-left
          :attendees-count="totalMembersCount"
          :each-draw-count="defaultAttendees.length"
          :prizes="prizes"
          :current-prize="currentPrize"
          :is-drawing="isDrawing"
          :show-total="showTotalNumber"
          @add="addPeople"
          @remove="removePeople"
          @select-award="selectAward" ref="drawLeftRef">
        </draw-left>
      </section>
      <section class="center" v-show="!isInitData">
        <p class="title" :class="{'hide-left-title': !isShowRight}">{{ flowStepRules.Settings.Title }} </p>
        <draw-center
          :default-attendees="defaultAttendees"
          :attendees="attendees"
          :is-draw-stop="showDrawList"
          :attendees-count="totalMembersCount"
          :show-custom-name="showCustomName"
          @start="startDraw"
          @stop="stopDraw">
        </draw-center>
      </section>
      <section class="right" v-show="isShowRight && !isInitData">
        <img @click="viewQrCode()" class="qrcode-img" src="../src/assets/qrcode.svg" v-if="flowStepRules.Settings.QRCode && flowStepRules.Settings.QRCode.AttachmentId">
        <draw-right
          :winners="winners"
          :show-custom-name="showCustomName"
        >
        </draw-right>
      </section>
      <section class="init-data-section" v-show="isInitData">
        <img class="init-img" src="../src/assets/loading.svg">
        <p class="init-label" >数据加载中...</p>
      </section>
    </main>
    <footer class="footer"></footer>
    <draw-qr-code
      :code-id="flowStepRules.Settings.QRCode ? flowStepRules.Settings.QRCode.AttachmentId : '' "
      :show-error="showError"
      @close="closeQrCode"
      v-show="showQrCode || showError">
    </draw-qr-code>
    <draw-list
      :award-name="currentPrize.RoundName"
      :show-custom-name="showCustomName"
      :winners="listWinners"
      :updating="isUpdateWinner"
      :is-picking="isPickingWinners"
      @close-list="closeDraw"
      v-if="showDrawList">
    </draw-list>
  </div>
</template>

<script>

import DrawLeft from '../src/components/DrawLeft'
import DrawCenter from '../src/components/DrawCenter'
import DrawRight from '../src/components/DrawRight'
import DrawQrCode from '../src/components/DrawQrCode'
import DrawList from '../src/components/DrawList'
import { getMembersCount, getMembers, pickWinners, updateFlowStep, getWinners, sendNotification } from './api/lucky-draw'
import { escape2Html } from './helpers/helper'

const pageDefaultHeight = 1180
const defaultAttendImg = require('../src/assets/avatar.svg')
const defaultAttendName = '虚位以待'
const maxRoundCount = 15

function getQueryString (name) {
  let reg = new RegExp(`(^|&)${name}=([^&]*)(&|$)`, 'i')
  let searchParas = window.location.search.substr(1).match(reg)
  if (searchParas) {
    return searchParas
  } else {
    return []
  }
}

function checkFull () {
  let isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
  // to fix : false || undefined == undefined
  if (isFull === undefined) {
    isFull = false
  }
  return isFull
}

export default {
  name: 'app',
  components: {
    DrawLeft,
    DrawCenter,
    DrawRight,
    DrawQrCode,
    DrawList
  },
  data () {
    return {
      defaultAttendees: [],
      attendees: [],
      showQrCode: false,
      showDrawList: false,
      flowStepId: '',
      flowStepRules: {},
      prizes: [],
      currentPrize: {},
      totalMembersCount: 0,
      currentRound: 0,
      showTotalNumber: true,
      showCustomName: true,
      listWinners: [],
      winners: [],
      showError: false,
      urlTestNumber: 0,
      isFullScreen: false,
      isDrawing: false,
      isShowRight: true,
      isUpdateWinner: false,
      isInitData: false,
      isPickingWinners: false
    }
  },
  async created () {
    this.flowStepId = getQueryString('flowStepId')[2]
    this.urlTestNumber = getQueryString('test')[2] || 0
    this.flowStepRules = JSON.parse(window.flowStepRules)
    this.flowStepRules.Prizes.sort((a, b) => a.Order - b.Order)
    this.prizes = this.flowStepRules.Prizes
    for (let prize of this.prizes) {
      let restPrizeCount = prize.Quantity - prize.Winners.length
      if (restPrizeCount > 0) {
        prize.Quantity = restPrizeCount
      } else {
        prize.Quantity = 0
      }
    }
    this.showCustomName = this.flowStepRules.Settings.IsShowLeadOrContactName
    this.showTotalNumber = this.flowStepRules.Settings.IsShowNumberOfParticipants
    this.currentRound = this.flowStepRules.CurrentRound
    this.currentPrize = this.prizes.find(prize => prize.Order === this.currentRound)
    if (!this.currentPrize) {
      this.currentPrize = {}
    }
    this.isInitData = true
    await Promise.all([this.fetchMembersCount(), this.fetchMembers(), this.fetchWinners()])
    // hide loading image when app background image was loaded
    let bgImg = new Image()
    let appEle = document.getElementById('app')
    let bgAttachmentId = this.flowStepRules.Settings.BackgroundAttachmentId
    bgImg.onload = () => {
      if (bgImg.width === 0) {
        this.showError = true
      } else {
        appEle.style.background = `url(${bgImg.src}) no-repeat`
        appEle.style.backgroundSize = 'cover'
        this.isInitData = false
      }
    }
    bgImg.onerror = () => {
      this.showError = true
    }
    if (bgAttachmentId) {
      bgImg.src = `/servlet/servlet.FileDownload?file=${bgAttachmentId}`
    } else {
      bgImg.src = require('../src/assets/page.jpg')
    }
    if (this.currentPrize.Quantity > 0) {
      let minCount = Math.min(this.currentPrize.Quantity, maxRoundCount, this.totalMembersCount)
      for (let i = 0; i < minCount; i++) {
        this.defaultAttendees.push({name: defaultAttendName, imgUrl: defaultAttendImg})
      }
    }
  },
  mounted () {
    this.resizePage()
    window.onresize = () => {
      // adjust 4/3 and 16/9 screen
      let screenObj = window.screen
      this.isShowRight = screenObj.availHeight / screenObj.availWidth <= 0.7
      this.resizePage()
      if (!checkFull()) {
        this.isFullScreen = false
      }
    }
  },
  methods: {
    async fetchMembersCount () {
      try {
        let result = await getMembersCount(this.flowStepId)
        if (result.ok) {
          let resultData = JSON.parse(escape2Html(result.data))
          this.totalMembersCount = resultData.MembersCount
        } else {
          this.showError = true
        }
      } catch (error) {
        this.showError = true
      }
    },
    async fetchMembers () {
      try {
        let result = await getMembers(this.flowStepId)
        if (result.ok) {
          let resultData = JSON.parse(escape2Html(result.data))
          this.attendees = resultData.Members
        } else {
          this.showError = true
        }
      } catch (error) {
        this.showError = true
      }
    },
    async fetchWinners () {
      try {
        let winnerResults = await getWinners(this.flowStepId)
        if (winnerResults.ok) {
          let winnerResultData = JSON.parse(escape2Html(winnerResults.data))
          this.winners = winnerResultData.WinnersResult
          this.winners.sort((a, b) => a.Order - b.Order)
        } else {
          this.showError = true
        }
      } catch (error) {
        this.showError = true
      }
    },
    resizePage () {
      let docEle = document
      let appEle = docEle.getElementById('app')
      let mainEle = docEle.getElementById('main')
      let qrcodeEle = docEle.getElementById('qrcode-img-content')
      let mainScaleY = appEle.offsetHeight / pageDefaultHeight
      mainEle.style.transform = `scale(${mainScaleY})`
      if (qrcodeEle) {
        qrcodeEle.style.transform = `scale(${mainScaleY})`
      }
    },
    addPeople () {
      this.defaultAttendees.push({name: defaultAttendName, imgUrl: defaultAttendImg})
    },
    removePeople () {
      this.defaultAttendees.pop()
    },
    selectAward (item) {
      this.currentRound = item.Order
      this.currentPrize = item
      this.defaultAttendees = []
      if (item.Quantity > 0) {
        let minCount = Math.min(this.currentPrize.Quantity, maxRoundCount, this.totalMembersCount)
        for (let i = 0; i < minCount; i++) {
          this.defaultAttendees.push({name: defaultAttendName, imgUrl: defaultAttendImg})
        }
      }
    },
    fullScreen () {
      const root = document.getElementById('app')
      if (root.requestFullscreen) {
        root.requestFullscreen()
      } else if (root.webkitRequestFullscreen) {
        root.webkitRequestFullscreen()
      } else if (root.mozRequestFullScreen) {
        root.mozRequestFullScreen()
      } else if (root.msRequestFullscreen) {
        root.msRequestFullscreen()
      }
      this.$refs.fullScreenEle.style.top = '-80px'
      this.isFullScreen = true
    },
    exitFullScreen () {
      if (document.exitFullscreen) {
        document.exitFullscreen()
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen()
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen()
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen()
      }
      this.isFullScreen = false
    },
    viewQrCode () {
      this.showQrCode = true
    },
    async closeQrCode () {
      if (!this.showError) {
        await Promise.all([this.fetchMembersCount(), this.fetchMembers(), this.fetchWinners()])
        this.defaultAttendees = []
        if (this.currentPrize.Quantity > 0) {
          let minCount = Math.min(this.currentPrize.Quantity, maxRoundCount, this.totalMembersCount)
          for (let i = 0; i < minCount; i++) {
            this.defaultAttendees.push({name: defaultAttendName, imgUrl: defaultAttendImg})
          }
        }
      }
      this.showQrCode = false
    },
    toggleFullScreen (event) {
      if (event.clientY > 0 && event.clientY <= 80) {
        this.$refs.fullScreenEle.style.top = '0px'
      } else {
        this.$refs.fullScreenEle.style.top = '-80px'
      }
    },
    async startDraw () {
      this.isDrawing = true
      if (!this.isPickingWinners) {
        try {
          this.isPickingWinners = true
          this.listWinners = []
          let result = await pickWinners(this.defaultAttendees.length, this.flowStepId, this.currentPrize.Order)
          if (result.ok) {
            let resultData = JSON.parse(escape2Html(result.data))
            this.listWinners = resultData.Winners
          } else {
            this.showError = true
          }
        } catch (error) {
          this.showError = true
        } finally {
          this.isPickingWinners = false
        }
      }
    },
    stopDraw () {
      this.isDrawing = false
      this.showDrawList = true
    },
    async closeDraw (winners = []) {
      let updateWinners = []
      let blockWinnerIds = []
      let campaignMemberIds = []
      for (let winner of winners) {
        if (winner.status === 'use') {
          let tempWinner = {
            FollowerId: winner.FollowerId,
            CampaignMemberId: winner.MemberId,
            IsCollected: false
          }
          campaignMemberIds.push(winner.MemberId)
          updateWinners.push(tempWinner)
        } else if (winner.status === 'ignore') {
          blockWinnerIds.push(winner.MemberId)
        }
      }
      let useWinners = winners.filter((winner) => winner.status === 'use')
      if (this.urlTestNumber) {
        for (let winner of useWinners) {
          winner.Order = this.currentPrize.Order
          winner.RoundName = this.currentPrize.RoundName
          this.winners.push(winner)
          this.winners.sort((a, b) => a.Order - b.Order)
        }
      } else {
        try {
          this.isUpdateWinner = true
          let result = await updateFlowStep(this.currentRound, this.flowStepId, JSON.stringify(updateWinners), blockWinnerIds)
          if (result.ok) {
            sendNotification(campaignMemberIds, this.currentPrize.Order, this.flowStepId)
              .catch(() => {
                this.showError = true
              })
            await this.fetchWinners()
            this.isUpdateWinner = false
          } else {
            this.showError = true
            this.isUpdateWinner = false
          }
        } catch (error) {
          this.showError = true
          this.isUpdateWinner = false
        }
      }
      // adjust prize quantity and default attend number
      const restPrizeCount = this.currentPrize.Quantity - useWinners.length
      if (restPrizeCount > 0) {
        if (this.defaultAttendees.length > restPrizeCount) {
          this.defaultAttendees = []
          for (let i = 0; i < restPrizeCount; i++) {
            this.defaultAttendees.push({name: defaultAttendName, imgUrl: defaultAttendImg})
          }
        } else {
          for (let defaultAttend of this.defaultAttendees) {
            defaultAttend.name = defaultAttendName
            defaultAttend.imgUrl = defaultAttendImg
          }
        }
        this.currentPrize.Quantity = restPrizeCount
      } else {
        let prizeIndex = 0
        for (let i = 0, len = this.prizes.length; i < len; i++) {
          let prize = this.prizes[i]
          if (this.currentRound === prize.Order) {
            prizeIndex = i
            break
          }
        }
        if (prizeIndex === this.prizes.length - 1) {
          this.currentPrize.Quantity = 0
          this.defaultAttendees = []
        } else {
          this.currentPrize.Quantity = 0
          this.defaultAttendees = []
          this.currentPrize = this.prizes[prizeIndex + 1]
          this.currentRound = this.currentPrize.Order
          if (this.currentPrize.Quantity > 0) {
            let minCount = Math.min(this.currentPrize.Quantity, maxRoundCount, this.totalMembersCount)
            for (let i = 0; i < minCount; i++) {
              this.defaultAttendees.push({name: defaultAttendName, imgUrl: defaultAttendImg})
            }
          } else {
            this.defaultAttendees = []
          }
        }
      }
      this.showDrawList = false
    },
    setDraw () {
      window.open(`${window.location.origin}/apex/CharketApp?#/wechat-campaign/lucky-draw?flowId=${window.flowId}&flowStepId=${this.flowStepId}`, 'WeChat Campaign | Charket')
    }
  }
}
</script>

