<template>
  <div id="draw-list">
    <header class="header">
      <span class="left-label">{{ awardName }}</span>
      <span class="right-label">中奖名单</span>
    </header>
    <main class="content" id="draw-list-content">
      <ul class="list" v-if="sortWinners.length > 0 || isPicking" key="winner-list">
        <li class="list-item" :style="{width: winner.imgWidth, height: winner.containerHeight}" v-for="(winner, index) in sortWinners" :key="index">
          <div class="list-item-bg" :style="{width: winner.imgWidth, height: winner.imgWidth}">
            <span class="bg-label" @click="ignoreWinner(winner)" v-if="winner.status === 'use'">忽 略</span>
            <span class="bg-label bg-use-label" @click="useWinner(winner)" v-if="winner.status === 'ignore'">启 用</span>
          </div>
          <img :style="{width: winner.imgWidth, height: winner.imgWidth}" class="item-img"
               :class="{'border-ignore': winner.status === 'ignore', 'border-use': winner.status === 'use'}"
               :src="winner.useImg">
          <span class="item-label" :style="{fontSize: winner.nameFontSize}" :class="{'ignore-name': winner.status === 'ignore'}" v-if="showCustomName">{{ winner.CampaignMemberName }}</span>
          <span class="item-label" :style="{fontSize: winner.nameFontSize}" :class="{'ignore-name': winner.status === 'ignore'}" v-if="!showCustomName">{{ winner.FollowerName }}</span>
        </li>
      </ul>
      <div class="pity-section" v-else key="winner-list">
        <img class="pity-img" src="../../src/assets/pity.svg">
        <p class="pity-label">很遗憾，没有人中奖</p>
      </div>
      <span class="confirm-btn" :class="{'confirm-disabled': updating || isPicking}" @click="close()">{{ confirmLabel }}</span>
    </main>
    <footer class="footer"></footer>
  </div>
</template>

<script>
const photoSize = {
  big: {
    imgWidth: '298px',
    nameFontSize: '40px',
    containerHeight: '405px'
  },
  normal: {
    imgWidth: '218px',
    nameFontSize: '30px',
    containerHeight: '325px'
  }
}
export default {
  name: 'draw-list',
  props: {
    winners: {
      type: Array,
      default () {
        return []
      }
    },
    awardName: {
      type: String,
      default () {
        return ''
      }
    },
    showCustomName: {
      type: Boolean,
      default () {
        return true
      }
    },
    updating: {
      type: Boolean,
      default () {
        return false
      }
    },
    isPicking: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      enterListener: null,
      defaultImg: require('../assets/avatar.svg')
    }
  },
  computed: {
    sortWinners: function () {
      let tempWinner = []
      for (let winner of this.winners) {
        if (this.winners.length <= 5) {
          let bigPhoto = photoSize.big
          this.$set(winner, 'imgWidth', bigPhoto.imgWidth)
          this.$set(winner, 'nameFontSize', bigPhoto.nameFontSize)
          this.$set(winner, 'containerHeight', bigPhoto.containerHeight)
        } else {
          let normalPhoto = photoSize.normal
          this.$set(winner, 'imgWidth', normalPhoto.imgWidth)
          this.$set(winner, 'nameFontSize', normalPhoto.nameFontSize)
          this.$set(winner, 'containerHeight', normalPhoto.containerHeight)
        }
        this.$set(winner, 'status', 'use')
        this.$set(winner, 'useImg', winner.FollowerUrl || this.defaultImg)
        tempWinner.push(winner)
      }
      return tempWinner
    },
    confirmLabel: function () {
      return this.updating ? '确认中' : '确认'
    }
  },
  created () {
    // listener enter to confirm
    this.enterListener = (e) => {
      e.stopPropagation()
      e.preventDefault()
      if (e.keyCode === 13 && !this.updating && !this.isPicking) {
        this.close()
      }
    }
    window.addEventListener('keyup', this.enterListener)
  },
  beforeDestroy () {
    window.removeEventListener('keyup', this.enterListener)
  },
  methods: {
    close () {
      this.$emit('close-list', this.sortWinners)
    },
    ignoreWinner (item) {
      item.status = 'ignore'
      item.useImg = require('../../src/assets/ignore.svg')
    },
    useWinner (item) {
      item.status = 'use'
      item.useImg = item.FollowerUrl || this.defaultImg
    }
  }
}
</script>
