<template>
  <section id="draw-center">
    <section class="box-person" v-if="defaultAttendees.length > 0" key="sort-people">
      <ul class="list" v-for="(item, index) in sortPeoples" :key="index">
        <li class="item" v-for="people in sortPeoples[index]" :key="people.id">
          <div class="item-section">
            <div class="item-photo" :style="{width: people.circleSize, height: people.circleSize, backgroundSize: people.circleSize}">
            </div>
            <img :style="{width: people.imgSize, height: people.imgSize}" class="item-photo-img" :src="people.imgUrl">
            <p class="item-name" :style="{fontSize: people.wordSize, margin: people.marginSize, width: people.imgSize}">{{ people.name }}</p>
          </div>
        </li>
      </ul>
    </section>
    <section class="box-person empty-person" v-else key="sort-people">
      <div v-if="attendeesCount > 0" key="member-count">
        <img class="empty-img" src="../../src/assets/empty.svg">
        <p class="empty-label-top">奖品已经抽完啦</p>
        <p class="empty-label-bottom">请重新设置</p>
      </div>
      <div v-else key="member-count">
        <img class="member-img" src="../../src/assets/member.svg">
        <p class="member-label">没有市场活动成员参与抽奖</p>
      </div>
    </section>
    <section class="box-start">
      <span class="box-btn" :class="{'disabled-btn' : defaultAttendees.length <= 0 || attendees.length <= 0}" @click="start()" v-show="!showStop">开始</span>
      <span class="box-btn" @click="stop()" v-show="showStop">停止</span>
    </section>
  </section>
</template>

<script>

const photoSize = {
  big: {
    circleWidth: '320px',
    imgWidth: '298px',
    nameFontSize: '40px',
    nameMargin: '15px 0'
  },
  normal: {
    circleWidth: '240px',
    imgWidth: '218px',
    nameFontSize: '30px',
    nameMargin: '15px 0'
  },
  small: {
    circleWidth: '180px',
    imgWidth: '158px',
    nameFontSize: '30px',
    nameMargin: '15px 0'
  },
  smallest: {
    circleWidth: '140px',
    imgWidth: '118px',
    nameFontSize: '20px',
    nameMargin: '15px 0'
  }
}

const defaultAttendImg = require('../../src/assets/avatar.svg')

export default {
  name: 'draw-center',
  props: {
    defaultAttendees: {
      type: Array,
      default () {
        return []
      }
    },
    attendees: {
      type: Array,
      default () {
        return []
      }
    },
    attendeesCount: {
      type: Number,
      default () {
        return 1
      }
    },
    showCustomName: {
      type: Boolean,
      default () {
        return true
      }
    },
    isDrawStop: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      imgInterval: null,
      showStop: false,
      spaceListener: null
    }
  },
  computed: {
    sortPeoples: function () {
      let tempPeoples = []
      let tempSortPeoples = []
      const peoplesLength = this.defaultAttendees.length
      const setPeople = (imgSize, wordSize, marginSize, circleSize) => {
        for (let i = 0; i < peoplesLength; i++) {
          let item = this.defaultAttendees[i]
          let tempPeople = {
            id: i,
            imgUrl: item.imgUrl,
            name: item.name,
            imgSize: imgSize,
            wordSize: wordSize,
            marginSize: marginSize,
            circleSize: circleSize
          }
          tempPeoples.push(tempPeople)
        }
      }
      const setSortPeople = () => {
        let arrayLength = tempPeoples.length
        let lastCount = Math.ceil(arrayLength / 2)
        tempSortPeoples.push(tempPeoples.slice(0, lastCount))
        tempSortPeoples.push(tempPeoples.slice(lastCount, arrayLength))
      }
      if (peoplesLength > 0 && peoplesLength < 3) {
        const bigPhotoSize = photoSize.big
        setPeople(bigPhotoSize.imgWidth, bigPhotoSize.nameFontSize, bigPhotoSize.nameMargin, bigPhotoSize.circleWidth)
        tempSortPeoples.push(tempPeoples)
      } else if (peoplesLength === 3) {
        const normalPhotoSize = photoSize.normal
        setPeople(normalPhotoSize.imgWidth, normalPhotoSize.nameFontSize, normalPhotoSize.nameMargin, normalPhotoSize.circleWidth)
        tempSortPeoples.push(tempPeoples)
      } else if (peoplesLength <= 6) {
        const normalPhotoSize = photoSize.normal
        setPeople(normalPhotoSize.imgWidth, normalPhotoSize.nameFontSize, normalPhotoSize.nameMargin, normalPhotoSize.circleWidth)
        setSortPeople()
      } else if (peoplesLength <= 8) {
        const smallPhotoSize = photoSize.small
        setPeople(smallPhotoSize.imgWidth, smallPhotoSize.nameFontSize, smallPhotoSize.nameMargin, smallPhotoSize.circleWidth)
        setSortPeople()
      } else if (peoplesLength <= 10) {
        const smallestPhotoSize = photoSize.smallest
        setPeople(smallestPhotoSize.imgWidth, smallestPhotoSize.nameFontSize, smallestPhotoSize.nameMargin, smallestPhotoSize.circleWidth)
        setSortPeople()
      } else if (peoplesLength <= 15) {
        const smallestPhotoSize = photoSize.smallest
        setPeople(smallestPhotoSize.imgWidth, smallestPhotoSize.nameFontSize, smallestPhotoSize.nameMargin, smallestPhotoSize.circleWidth)
        let arrayLength = tempPeoples.length
        let integerCount = parseInt(arrayLength / 3)
        let remainderCount = arrayLength % 3
        switch (remainderCount) {
          case 0:
            tempSortPeoples.push(tempPeoples.slice(0, integerCount))
            tempSortPeoples.push(tempPeoples.slice(integerCount, integerCount * 2))
            tempSortPeoples.push(tempPeoples.slice(integerCount * 2, arrayLength))
            break
          case 1:
            tempSortPeoples.push(tempPeoples.slice(0, integerCount + 1))
            tempSortPeoples.push(tempPeoples.slice(integerCount + 1, integerCount * 2 + 1))
            tempSortPeoples.push(tempPeoples.slice(integerCount * 2 + 1, arrayLength))
            break
          case 2:
            tempSortPeoples.push(tempPeoples.slice(0, integerCount + 1))
            tempSortPeoples.push(tempPeoples.slice(integerCount + 1, integerCount * 2 + 2))
            tempSortPeoples.push(tempPeoples.slice(integerCount * 2 + 2, arrayLength))
            break
          default:
        }
      }
      return tempSortPeoples
    }
  },
  created () {
    // listener space to start draw
    this.spaceListener = (e) => {
      e.preventDefault()
      e.stopPropagation()
      if (!this.isDrawStop && this.defaultAttendees.length > 0 && this.attendees.length > 0) {
        if (e.keyCode === 32) {
          if (!this.showStop) {
            this.start()
          } else {
            this.stop()
          }
        }
      }
    }
    window.addEventListener('keyup', this.spaceListener)
  },
  beforeDestroy () {
    window.removeEventListener('keyup', this.spaceListener)
  },
  methods: {
    start () {
      this.showStop = true
      let attendeeImgs = []
      let attendeeNames = []
      for (let attendee of this.attendees) {
        attendeeImgs.push(attendee.FollowerUrl)
        if (this.showCustomName) {
          attendeeNames.push(attendee.CampaignMemberName)
        } else {
          attendeeNames.push(attendee.FollowerName)
        }
      }
      this.imgInterval = setInterval(() => {
        for (let people of this.defaultAttendees) {
          const index = Math.floor((Math.random() * attendeeNames.length))
          people.imgUrl = attendeeImgs[index] || defaultAttendImg
          people.name = attendeeNames[index]
        }
      }, 80)
      this.$emit('start')
    },
    stop () {
      clearInterval(this.imgInterval)
      this.showStop = false
      this.$emit('stop')
    }
  }
}
</script>

<style lang="scss" scoped>
  #draw-center {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .box-person {
      display: flex;
