          }
        }
      }
    }
    .empty-person {
      justify-content: center;
      text-align: center;
      .empty-img {
        width: 185px;
        height: 210px;
      }
      .empty-label-top {
        margin: 0;
        margin-top: 41px;
        color: #202020;
        font-size: 26px;
      }
      .empty-label-bottom {
        margin: 0;
        font-size: 26px;
      }
      .member-img {
        width: 220px;
        height: 220px;
      }
      .member-label {
        font-size: 30px;
        color: #9C9C9C;
        margin: 0;
        margin-top: 25px;
      }
    }
    .box-start {
      margin-top: 10px;
      display: flex;
      align-items: center;
      justify-content: space-around;
      height: 160px;
      .box-btn {
        color: #FFFFFF;
        width: 258px;
        height: 58px;
        text-align: center;
        line-height: 58px;
        font-size: 30px;
        background: rgb(92, 152, 255);
        border-radius: 29px;
        outline: none;
        cursor: pointer;
        border: 0;
      }
      .disabled-btn {
        pointer-events: none;
        background: #656565;
      }
    }
    @keyframes play{
      0%  {
        transform: rotate(360deg);
      }
      100% {
        transform: rotate(0deg);
      }
    }
  }
</style>
