import sfdcRemoting from './sfdc-remoting.js'

export function getMembersCount (flowStepId) {
  return sfdcRemoting({
    method: 'LuckyDrawController.getMembersCount',
    args: [ flowStepId ]
  })
}

export function getMembers (flowStepId) {
  return sfdcRemoting({
    method: 'LuckyDrawController.getMembers',
    args: [ flowStepId ]
  })
}

export function pickWinners (quantity, flowStepId, prizeOrder) {
  return sfdcRemoting({
    method: 'LuckyDrawController.pickWinners',
    args: [ quantity, flowStepId, prizeOrder ]
  })
}

export function updateFlowStep (currentRound, flowStepId, winners, blockedMemberIds) {
  return sfdcRemoting({
    method: 'LuckyDrawController.updateFlowStep',
    args: [ currentRound, flowStepId, winners, blockedMemberIds ]
  })
}

export function getWinners (flowStepId) {
  return sfdcRemoting({
    method: 'LuckyDrawController.getWinners',
    args: [ flowStepId ]
  })
}

export function sendNotification (campaignMemberIds, prizeOrder, flowStepId) {
  return sfdcRemoting({
    method: 'LuckyDrawController.sendNotification',
    args: [ campaignMemberIds, prizeOrder, flowStepId ]
  })
}
