<template>
  <section id="draw-left">
    <div class="awards-label">
      <p class="title">当前奖项</p>
      <div class="award-detail" :class="{'disabled-award-detail': isDrawing}" v-clickoutside="closeAwardList">
        <p class="name" @click="openAwardList">{{ currentPrize.RoundName }}</p>
        <button class="btn" :class="{'disabled-award-btn': isDrawing}" @click="openAwardList">
          <span class="btn-label"></span>
        </button>
        <ul class="award-list" v-show="showAwardItems">
          <li class="award-item" :class="{'selected-item': prize.Order === currentPrize.Order}" v-for="(prize, index) in prizes" :key="index" @click="selectAward(prize)">
            <span>{{ prize.RoundName }}</span>
          </li>
        </ul>
      </div>
    </div>
    <img class="img" :src="'/servlet/servlet.FileDownload?file=' + currentPrize.PictureAttachmentId" v-if="currentPrize.PictureAttachmentId">
    <div class="bg-img" v-if="!currentPrize.PictureAttachmentId"></div>
    <p class="img-name">{{ currentPrize.PrizeName }}</p>
    <p class="award-number">数量 {{ currentPrize.Quantity }}</p>
    <section class="set-content">
      <p class="count-label" v-if="showTotal">抽奖参与人数</p>
      <p class="count-value top" v-if="showTotal">{{ attendeesCount }}</p>
    </section>
    <section class="set-content">
      <p class="count-label">每次抽取人数</p>
      <span class="count-btn" :class="{'disabled-color': eachDrawCount <= 1 || isDrawing}" @click="remove()">
        <span class="btn-label">-</span>
      </span>
      <p class="count-value">{{ eachDrawCount }}</p>
      <span class="count-btn" :class="{'disabled-color': eachDrawCount >= 15 || isDrawing ||
      this.eachDrawCount >= this.attendeesCount || this.eachDrawCount >= this.currentPrize.Quantity}" @click="add()">
        <span class="btn-label">+</span>
      </span>
    </section>
  </section>
</template>

<script>
export default {
  name: 'draw-left',
  props: {
    prizes: {
      type: Array,
      default () {
        return []
      }
    },
    currentPrize: {
      type: Object,
      default () {
        return {}
      }
    },
    showTotal: {
      type: Boolean,
      default () {
        return true
      }
    },
    attendeesCount: {
      type: Number,
      default () {
        return 1
      }
    },
    eachDrawCount: {
      type: Number,
      default () {
        return 1
      }
    },
    isDrawing: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  created () {
    // listener up and down to add or remove attendees
    this.upAndDownListener = (e) => {
      e.stopPropagation()
      e.preventDefault()
      if (e.keyCode === 38) {
        if (this.eachDrawCount < 15 && !this.isDrawing &&
          this.eachDrawCount < this.attendeesCount && this.eachDrawCount < this.currentPrize.Quantity) {
          this.add()
        }
      } else if (e.keyCode === 40) {
        if (this.eachDrawCount > 1 && !this.isDrawing) {
          this.remove()
        }
      }
    }
    window.addEventListener('keyup', this.upAndDownListener)
  },
  beforeDestroy () {
    window.removeEventListener('keyup', this.upAndDownListener)
  },
  data () {
    return {
      showAwardItems: false,
      upAndDownListener: null
    }
  },
  methods: {
    add () {
      this.$emit('add')
    },
    remove () {
      this.$emit('remove')
    },
    selectAward (item) {
      this.showAwardItems = false
      this.$emit('select-award', item)
    },
    openAwardList () {
      this.showAwardItems = true
    },
    closeAwardList () {
      this.showAwardItems = false
    }
  }
}
</script>

